.ChiTiet {
  width: calc(100% - 200px);
  margin-left: 100px;
  margin-top: 40px;
  padding-top: 20px;
  background-color: white;

  hr {
    width: 150px;
    border: 2px solid #35973F;
    opacity: 1;
    margin-bottom: 10px;
    margin-top: 40px;
    text-align: left;
  }

  pre {
    white-space: pre-wrap; // Giữ nguyên ngắt dòng và cho phép ngắt dòng tự động
    word-wrap: break-word; // Tự động ngắt dòng khi từ quá dài
  }
}

.left {
  padding-left: 50px;
}

.left .time {
  margin-left: 10px;
  border-left: 3px solid #001273;
  padding-left: 40px;
}

.left .inner-title {
  font-size: 28px;
  font-weight: 700;
  color: #001273;
  margin-bottom: 5px;
  line-height: 100%;
}

.left .desc {
  font-size: 24px;
  font-weight: 600;
  color: #4F4F4F;
  margin-bottom: 30px;  
}

.title {
  font-size: 30px;
  font-weight: 600;
  color: #35973F;
  margin-bottom: 20px;
}

.right {
  padding-right: 50px;
}

.right .register {
  display: flex;
  justify-content: space-between;
  padding-right: 30px;
  align-items: center;
  margin-bottom: 10px;
}

.right .register .button {
  padding: 5px 20px;
  border: none;
  border-radius: 8px;
  font-size: 24px;
  font-weight: 600;
  color: white;
  background-color: #1A931F;
}

.right .desc {
  font-size: 28px;
  font-weight: 600;
  color: #4F4F4F;
  font-family: 'montserrat', sans-serif;
  line-height: 200%;
}

.plan-row {
  padding-left: 50px;
  padding-right: 50px;
}

.plan-row .desc {
    color: black;
    font-size: 22px;
    font-weight: 500;
    text-align: justify;
    font-family: 'montserrat', sans-serif;
}

.button-history {
  padding: 5px 20px;
  border: none;
  border-radius: 8px;
  font-size: 24px;
  font-weight: 600;
  color: white;
  background-color: #1A931F;
  margin-top: 20px;
}

.quy {
  display: flex;
  justify-content: space-between;
  width: 70%;
  margin-left: auto;
  margin-right: auto;
  background-color: #b5ffb4;
  // padding: 50px;
  margin-top: 50px;
  margin-bottom: 50px;
  border-radius: 10px;
}
.quy .title {
  text-align: center;
  margin-bottom: 0;
  color: #1A931F;
  font-size: 22px;
  font-weight: 700;
}

.quy {
  label {
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 0px;
    margin-top: 10px;
    color: #737373;
  }

  input {
    width: 100%;
    border: 1px solid #c9c9c9;
    border-radius: 5px;
    padding-left: 10px;
    font-size: 18px;
    font-weight: 500;
    color: black;

    &:focus-visible {
      outline: none;
      border: 1px solid #5fa967;
      box-shadow: none;
    }
  }

  button {
    padding: 5px 20px;
    border: none;
    border-radius: 8px;
    font-size: 18px;
    font-weight: 600;
    color: white;
    background-color: #1A931F;
    margin-top: 20px;
    margin-bottom: 0px;
  }
}

.button {
  text-align: center;
}


.quy-row {
  padding: 0 50px;
}

.quy .qr-code {
  height: 565px;
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url("../../../../../Images/Quy/Donate/background_intro.webp");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 10px;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

.quy .qr-code .img-qr {
  height: 80%;
  width: auto;
  text-align: center;
  img {
    height: 100%;
    height: 100%;
    object-fit: contain;
    border-radius: 10px;
  }
}

.quy .transfer-form {
  width: 50%;
  padding: 50px;
}

.line {
  border: 1px solid #e3e3e3;
  width: calc(100% - 100px);
  margin-left: 50px;
  margin-top: 30px;
}

.modal, .modal-history, .modal-register {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: white;
  padding: 20px;
  z-index: 1000;
  border-radius: 8px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

.overlay, .overlay-history {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 999; /* Phải nhỏ hơn z-index của modal */
}

.modal {
  h2 {
    color: #02a308;
  }
}

