.fun-history {
  width: 100%;
  margin-bottom: 80px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.fun-history .title {
  font-size: 26px;
  font-weight: bold;
  color:  #1A931F;
  margin-bottom: 15px;
  text-align: left;
  width: 100%;
  margin-left: 30%;
}


.fun-history .table-container {
  max-height: 600px; 
  overflow-y: auto; 
  overflow-x: hidden;
  border-radius: 10px;
  overflow: scroll;
  width: 70%;
  scrollbar-width: thin; 
  scrollbar-color: #878787 transparent;
  border: 0.5px solid #87878768;
}


.table-fun-history {
  font-size: 20px;
  font-family: 'montserrat';
  font-weight: 600;
  border-radius: 12px;
  overflow: hidden;
  overflow-x: auto;
}

.table-fun-history tr td{
  padding: 15px;
  white-space: nowrap;
}

.table-fun-history th{
  padding: 15px;
  white-space: nowrap;
}

.table-fun-history .time {
  text-align: right;
  max-width: 200px;
  padding-right: 50px;
  color: #333;
}

.table-fun-history .money {
  font-weight: bold;
  color: #02a308;
}

.location {
  margin-bottom: 15px;
}

.location button {
  border: none;
  background-color: transparent;
  line-height: 100%;
  font-size: 28px;
  color: #35973F;

  &:hover {
    cursor: pointer;
    color: #00ca07;
    transition: 0.3s;
    scale: 1.1;
  }
}

.location-desc {
  font-size: 24px;
  font-family: 'montserrat';
  font-weight: 600;
  color: #4F4F4F;
  line-height: 100%;
  margin-left: 10px;
}



