@media screen and (max-width: 1500px) {
  .right .register {
    flex-direction: column;
    align-items: start;
  }

  .quy {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 70%;
    padding: 50px;
  }
  
  .quy {
    label {
      font-size: 18px;
      font-weight: 600;
      margin-bottom: 0px;
      margin-top: 15px;
    }
  
    input {
      padding-left: 10px;
      font-size: 18px;
    }
  
    button {
      font-size: 18px;
      margin-top: 20px;
    }
  }
  
  .quy .transfer-form {
    width: 100%;
    margin-bottom: 20px;
  }

}

@media screen and (max-width: 1199.8px) {
  .ChiTiet {
    width: calc(100% - 120px);
    margin-left: 60px;
  }
  .right .desc {
    font-size: 18px;
  }

  .location {
    margin-bottom: 10px;
  }
  
  .location button {
    border: none;
    background-color: transparent;
    line-height: 100%;
    font-size: 20px;
    color: #35973F;
  
    &:hover {
      cursor: pointer;
      color: #00ca07;
      transition: 0.3s;
      scale: 1.1;
    }
  }
  
  .location-desc {
    font-size: 18px;
    font-family: 'montserrat';
    font-weight: 600;
    color: #4F4F4F;
    line-height: 100%;
    margin-left: 10px;
  }

  .quy-row {
    padding: 0 50px;
  }

