.UpdateCampaign {
  width: 100%;
  padding: 50px 200px;

  h1 {
    font-size: 40px;
    font-weight: 700 ;
    text-align: center;
    color: #35973F;
  }

  hr {
    width: 100px;
    border: 2px solid #35973F;
    opacity: 1;
    margin-bottom: 10px;
    margin-top: 0px;
  }

  label {
    font-size: 22px;
    font-weight: 600;
    color: black;
  }

  input, textArea {
    width: 100%;
    border: 1px solid #c9c9c9;
    border-radius: 5px;
    padding: 10px;
    font-size: 22px;
    font-weight: 500;
    color: black;

    &:focus-visible {
      outline: none;
      border: 1px solid #5fa967;
    }
  }
}

.title {
  font-size: 28px;
  font-weight: 600;
  color: #35973F;
  margin-bottom: 20px;
}

.input-name {
  height: 80px;
  margin-bottom: 15px;
}

.input-desc {
  height: 200px;
  margin-bottom: 20px;
}

.date {
  display: flex;
  justify-content: left;
  margin-bottom: 15px;
}

.date label {
  display: inline-block;
  width: 220px;
}

.date input {
  width: 200px;
  height: 40px;
}

.where {
  display: flex;
  flex-direction: column;
}

.item-where {
  display: flex;
  justify-content: center;
  margin-bottom: 15px;
}

.province {
  display: inline-block;
  width: 200px;
}

.district {
  display: inline-block;
  width: 200px;
}


.input-province, .input-district {
  display: inline-block;
  width: 300px;
  height: 40px;
  font-size: 18px;
  color: black;

  &:focus-visible {
    outline: none;
    border: 1px solid #5fa967;
  }
} 

.money-row {
  display: flex;
  margin-bottom: 40px;
  margin-top: 5px;
}

.money {
  display: flex;
  align-items: center;
  justify-content: left;
}

.total-money, .money-by-VNJN {
  display: inline-block;
  width: 220px;
}

.input-total-money, .input-money-by-VNJN {
  height: 40px;
  flex: 1;
  width: 200px;
  font-size: 30px;
  font-weight: bold;
  color: #35973F;
  margin-right: 100px;
}

.timeline-row {
  display: flex;
  justify-content: left;
  margin-bottom: 40px;
}

.timeline-row .timeline {
  display: inline-block;
}

