//   padding-left: 40px;
//   width: 300px;
// }

.time .inner-title {
  font-size: 24px;
  font-weight: 700;
  color: #001273;
  margin-bottom: 5px;
  line-height: 100%;
}

.time .desc {
  font-size: 22px;
  font-weight: 600;
  color: #737373;
  margin-bottom: 20px;  
  height: 80px;
}

.location-button {
  padding: 5px 20px;
  border: none;
  background-color: #35973F;
  border-radius: 8px;
  font-size: 18px;
  font-weight: 600;
  color: white;
  margin-left: 30px;
}

.row-location {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
  margin-top: 20px;

  input{
    width: 100%;
    border: 1px solid #c9c9c9;
    border-radius: 5px;
    padding: 10px;
    font-size: 22px;
    font-weight: 500;
    color: black;
    background-color: white;
  }

  button {
    margin-left: 20px;
    padding: 0px;
    border: none;
    background-color: transparent;
    color: rgb(170, 0, 0);
    font-size: 30px;
  }
}

.input-location {
  height: calc(100% - 100px);
  margin-top: 5px;
}

.contact-row {
  button {
    padding: 10px 30px;
    border: none;
    background-color: #35973F;
    border-radius: 8px;
    font-size: 18px;
    font-weight: 600;
    color: white;
  }

  input {
    width: 80%;
    font-size: 22px;
    font-weight: 500;
    padding: 5px 10px;
    margin-top: 10px;

    &:focus-visible {
      outline: none;
      border: 1px solid #5fa967;
      box-shadow: none;
    }
  }
}

.form-contact {
  margin-bottom: 30px;
}

.image-upload-container {
  text-align: center;
}

.button-container {
  display: flex;
  justify-content: center;
  margin-top: 30px;
  margin-top: 60px;

  label {
    padding: 10px 30px;
    border: none;
    background-color: #35973F;
    border-radius: 8px;
    font-size: 18px;
    font-weight: 600;
    color: white;
  }
}
.image-upload-button {
  display: inline-block;
}
.image-preview-container {
  width: 60%;
  height: auto;
  aspect-ratio: 2/1;
  margin-top: 20px;
  margin-left: 20%;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 10px;
  }
}

.submit-button {
  display: block;
  margin: 0 auto; 
  margin-top: 80px;
  padding: 10px 30px;
  border: none;
  background-color: #ffffff;
  border-radius: 8px;
  font-size: 20px;
  font-weight: 800;
  color: #35973F;
  border: 3px solid #35973F;
}