@media (max-width : 1050px) {
    .sticky-search-bar input {
        width: 92%;
    }

    .container3 {
        display: none;
    }

    .sticky {
        display: none;
    }
}

@media (max-width : 770px) {
    .sticky-search-bar input {
        width: 92%;
        
    }.sticky-search-bar {
        margin-top: 0.7rem;
    }
}

@media (max-width : 600px) {
    .sticky-search-bar input {
        width: 90%;
